import { css } from 'emotion';

const appHeader = css({
  "background-color": "#7433f1"
})

const appTitle = css({
  "margin": "0",
  "padding": "3% 0",
  "text-align": "center",
  "color": "white",
  "font-size": "30px"
})

const tables = css({
  "display": "inline-flex"
})

export {
  appHeader,
  appTitle,
  tables
}




